import { TO_SHOW } from '../components/Notification'
export const types = {
  APPLICATION_START: 'APPLICATION_START',
  ADD_NOTIFICATION: 'ADD_NOTIFICATION',
  FADE_NOTIFICATION: 'FADE_NOTIFICATION',
  REMOVE_NOTIFICATION: 'REMOVE_NOTIFICATION'
}


export const actions = {
  applicationStart: () => (dispatch) => dispatch({
    type: types.APPLICATION_START
  }),
  addNotification(notificationType, params){
    return {
      type: types.ADD_NOTIFICATION,
      notification: { type: notificationType, ...params }
    }
  },
  clearOld(){
    return (dispatch, getState) => {
      const notifications = getState().app.get('notifications')

      for(let notification of notifications.slice(0, notifications.size - TO_SHOW)){
        dispatch(actions.removeNotification([notification.get('created')]))
      }

    }
  },
  removeNotification(key){
    return {
      type: types.REMOVE_NOTIFICATION,
      key
    }
  },
  deleteNotification(created){
    return (dispatch) => {
      dispatch(actions.removeNotification([created]))
    }
  }
}


