import React, { Component, PropTypes } from 'react'
import { INFO } from '../App'
import './notification-items.css'
import classNames from 'classnames'

export default class NotificationItem extends Component {
  static propTypes = {
    header: PropTypes.string.isRequired,
    body: PropTypes.string.isRequired,
    type: PropTypes.string.isRequired,
    lifetime: PropTypes.number.isRequired,
    created: PropTypes.any.isRequired,
    hideCallback: PropTypes.func
  }

  componentDidMount() {
    this.setTimeOut()
  }

  componentWillUnmount(){
   this.clearTimeOut()
  }

  setTimeOut = () => {
    const { lifetime, type } = this.props
    if(type === INFO) {
      this.timeoutHandler = setTimeout(this.hide, lifetime)
    }
  }

  clearTimeOut = () => {
    if(this.timeoutHandler) {
      clearTimeout(this.timeoutHandler)
    }
  }

  hide = () => {
    const { hideCallback, created } = this.props
    if(hideCallback) {
      hideCallback(created)
    }
  }

  render() {
    const { header, body, type } = this.props
    return (
      <div
        className={classNames('notification-item', type)}
        onMouseOver={this.clearTimeOut}
        onMouseLeave={this.setTimeOut}
      >
        <div className="header">
          <div className="element title">{header}</div>
          <div className="element status">{type}</div>
          <div onClick={this.hide} className="element close" />
        </div>
        <div className="body">{body}</div>
      </div>
    )
  }
}



