import { actions, types } from '../actions/appActions'
import { fromJS } from 'immutable'


const initialState = fromJS({
  notifications: []
})


export default (state = initialState, action) => {
  let newState = state
  switch (action.type) {

  case types.ADD_NOTIFICATION:
    newState = newState.set('notifications', newState.get('notifications').push(fromJS(action.notification)))
    break
  case types.REMOVE_NOTIFICATION:
    if(Array.isArray(action.key)) {
      action.key.forEach(key => {
        newState = newState.deleteIn(['notifications', newState.get('notifications')
          .findKey(v => v.get('created') === key)])
      })

    }
    break
  default:
    break
  }
  return newState
}


