import React, { Component } from 'react';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { actions } from './actions/appActions.js'
import Notification from './components/Notification'
import './App.css';

export const LIFETIME = 'lifetime'
export const INFO = 'info'
export const WARNING = 'warning'
export const ERROR = 'error'
export const HEADER = 'header'
export const BODY = 'body'

export const DEFAULT_HEADER = 'Simple Notification Title'
export const DEFAULT_BODY = 'Simple Notification Body'
export const DEFAULT_LIFETIME = 5

class App extends Component {


  constructor(props) {
    super(props)
    this.state = {
      header: DEFAULT_HEADER,
      body: DEFAULT_BODY,
      lifetime: DEFAULT_LIFETIME
    }
  }

  componentDidMount() {
    this.props.actions.applicationStart()
  }

  /**
   * Removes notification
   * @param created
   */
  handleHideItem = (created) => {
    const { actions:{ deleteNotification } } = this.props
    deleteNotification(created)
  }

  /**
   * Add new Notification in a store
   * @param type
   */
  addNotification = (type) => {
    const { header, body, lifetime } = this.state
    const d = new Date()
    let createdKey = `${d.getTime()}.${d.getMilliseconds()}`;
    this.props.actions.addNotification(type, {
      header,
      body,
      lifetime: lifetime * 1000,
      created: createdKey
    })
  }

  /**
   * Test form handler
   * @param field
   * @param event
   */
  changeValue = (field, event) => {
    const { target: { value } } = event
    if(field === LIFETIME && !(/^\d+$/).exec(value) && value.length > 0) {
      return
    }
    this.setState({ [field]: value })
  }

  render() {
    const { notifications } = this.props.app.toJS()
    return (
      <div className="container">
        <div>
          <button
            onClick={this.addNotification.bind(this, INFO)}
          >
            Info
          </button>
          <button
            onClick={this.addNotification.bind(this, WARNING)}
          >
            Warning
          </button>
          <button
            onClick={this.addNotification.bind(this, ERROR)}
          >
            Error
          </button>
        </div>
        <div>
          header <input
          className="input-control" type="text" value={this.state.header}
          onChange={this.changeValue.bind(null, HEADER)}
        /><br/>
          body <input
          onChange={this.changeValue.bind(null, BODY)}
          className="input-control" type="text" value={this.state.body}
        /><br/>
          info lifetime(sec) <input
          className="input-control"
          type="text" onChange={this.changeValue.bind(null, LIFETIME)}
          value={this.state.lifetime}
        />
        </div>
        <Notification clear={this.props.actions.clearOld} notifications={notifications} hideItem={this.handleHideItem}/>
      </div>
    );
  }
}

export default connect(state => ({
  app: state.app
}), (dispatch) => ({
  actions: bindActionCreators(actions, dispatch)
}))(App)



