import React, { Component, PropTypes } from 'react'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'
import NotificationItem from './NotificationItem'
import NotificationGroup from './NotificationGroup'
import './notification.css'

export const TO_SHOW = 5

export default class Notification extends Component {

  static propTypes = {
    notifications: PropTypes.arrayOf(PropTypes.object).isRequired,
    hideItem: PropTypes.func.isRequired,
    clear: PropTypes.func
  }

  render() {
    const { notifications, hideItem, clear } = this.props
    const notificationsToShow = notifications.length > TO_SHOW
      ?
      notifications.slice(notifications.length - TO_SHOW)
      :
      notifications
    return (
      <div className="notification">
        {
          notifications.length > TO_SHOW
            ?
            <NotificationGroup clear={clear} grouped={notifications.slice(0, notifications.length - TO_SHOW)}/>
            :
            null
        }
        <ReactCSSTransitionGroup
          transitionName="fade"
          transitionEnterTimeout={500}
          transitionLeaveTimeout={300}
        >
          {notificationsToShow.map((notification, key) =>
            <NotificationItem key={notification.created} {...notification} hideCallback={hideItem}/>
          )}
        </ReactCSSTransitionGroup>
      </div>
    )
  }
}



