import React, { Component, PropTypes } from 'react'
import classNames from 'classnames'
import './notificationGroup.css'

export default class NotificationGroup extends Component {

  static propTypes = {
    grouped: PropTypes.arrayOf(PropTypes.object),
    clear: PropTypes.func
  }

  render() {
    const { grouped, clear } = this.props

    const statistic = grouped.reduce((st, notification) => {
      if(st[notification.type]) {
        st[notification.type] += 1
      } else {
        st[notification.type] = 1
      }
      return st
    }, {})

    return (
      <div className="notification-group">
        {this.renderStatistic(statistic)}
        <div className="item clear" onClick={clear} />
      </div>
    )
  }

  renderStatistic(statistic) {
    const elements = []
    for(let st in statistic){
      if(statistic.hasOwnProperty(st)){
        elements.push(<div key={st} className={classNames('item', st)}>{statistic[st]}</div>)
      }
    }
    return elements
  }
}


